import { Component, Pipe, PipeTransform } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';
import { Values } from '../../../providers/service/values';
import { Functions } from '../../../providers/service/functions';
import { CheckoutService } from '../../../providers/service/checkout-service';
//import { OrderSummary } from '../order-summary/order-summary';
//import { PrivacyPolicy } from '../privacy-policy/privacy-policy';


@Pipe({name: 'keys'})
export class KeysPipe implements PipeTransform {
  transform(value, args:string[]) : any {
    let keys = [];
    for (let key in value) {
      keys.push({key: key, value: value[key]});
      console.log(keys);
    }
    return keys;
  }
}

@IonicPage()
@Component({
  templateUrl: 'shipping-payment-method.html',
})


export class ShippingPaymentMethod {

  info: any;
  method: any;
  buttonText: any;
  payment_method: any;
  shippingMethods: any;
  public disableSubmit: boolean = false;
   

  constructor(public nav: NavController, public service: CheckoutService, public functions: Functions, public values: Values) {

  	this.method = {};
    this.method.agree=true;
    this.info = {};
    this.buttonText = this.values.lan.text_button_continue;
    this.method.comment= "";

  	this.service.getShippingPayment()
    .then((results) => this.shippingMethods = results);        
  }

  
  saveMethods(a){
    if(this.validateForm()){
      this.disableSubmit= true;
      this.buttonText= this.shippingMethods.text_loading;   

      this.service.saveMethods(a)
      .then((results) => this.handleResults(results));
    } 
  }

 
  handleResults(results){
    this.disableSubmit = false;
    this.buttonText= this.shippingMethods.button_continue;    
    this.nav.push('OrderSummary');
  }


  validateForm(){
    if(this.method.shipping == undefined){this.functions.showAlert(this.values.lan.text_error, this.values.lan.error_shipping); return false}
    if(this.method.payment == undefined){this.functions.showAlert(this.values.lan.text_error, this.values.lan.error_payment); return false}
    if(this.method.agree == undefined || this.method.agree == false ){this.functions.showAlert(this.values.lan.text_error, this.values.lan.error_agree); return false}
   
    return true;
  }

  
  encodeUrl(href) {
    return href.replace(/&amp;/g, '&');
  }

  
  loadPolicy(){
    this.info.agree_link = this.encodeUrl(this.service.paymentMethods.agree_link);
    this.info.title = this.service.paymentMethods.agree_title;
    this.nav.push('PrivacyPolicy', this.info);
  }

}






